___CSS_LOADER_EXPORT___.push([module.id, "\n.tao-h4[data-v-25873168] {\n\tfont-size: 100%;\n\tpadding: 0 0 1em 2em;\n\tborder-bottom: 1px solid var(--color-border);\n\tfont-weight: bold;\n\tmargin: 1em 0;\n}\n.appt-slider[data-v-25873168] {\n\tmargin-bottom: 3em;\n\tmargin-right: 0;\n\tbox-sizing: content-box;\n}\n.pml-cont[data-v-25873168] {\n\tposition: relative;\n}\n.pml-m[data-v-25873168],\n.pml-p[data-v-25873168] {\n\tfont-size: 125%;\n\tposition: absolute;\n\theight: 1em;\n\twidth: 1em;\n\tline-height: 1em;\n\ttop: 50%;\n\tmargin-top: -.5em;\n\ttext-align: center;\n\tcursor: pointer;\n\tcolor: var(--color-text-light);\n}\n.pml-m[data-v-25873168]:hover,\n.pml-p[data-v-25873168]:hover {\n\tcolor: var(--color-main-text);\n\ttransform: scale(1.2);\n}\n.pml-p[data-v-25873168] {\n\tright: 1.375em;\n}\n.pml-m[data-v-25873168] {\n\tright: 0;\n}\n.actions-cont[data-v-25873168] {\n\tdisplay: flex;\n\tflex-direction: row;\n\tmargin-top: 2em;\n}\n.actions-cont-button[data-v-25873168] {\n\tmargin-right: 1em;\n\tmin-width: 5em;\n}\n", "",{"version":3,"sources":["webpack://./src/components/modals/WeeklyApptsEditorModal.vue"],"names":[],"mappings":";AAsOA;CACA,eAAA;CACA,oBAAA;CACA,4CAAA;CACA,iBAAA;CACA,aAAA;AACA;AAEA;CACA,kBAAA;CACA,eAAA;CACA,uBAAA;AACA;AAEA;CACA,kBAAA;AACA;AAEA;;CAEA,eAAA;CACA,kBAAA;CACA,WAAA;CACA,UAAA;CACA,gBAAA;CACA,QAAA;CACA,iBAAA;CACA,kBAAA;CACA,eAAA;CACA,8BAAA;AACA;AAEA;;CAEA,6BAAA;CACA,qBAAA;AACA;AAEA;CACA,cAAA;AACA;AAEA;CACA,QAAA;AACA;AAEA;CACA,aAAA;CACA,mBAAA;CACA,eAAA;AACA;AAEA;CACA,iBAAA;CACA,cAAA;AACA","sourcesContent":["<script setup>\nimport {\n\tNcButton,\n\tNcModal,\n\tNcTextField\n} from \"@nextcloud/vue\";\nimport LabelAccordion from \"../LabelAccordion.vue\";\nimport VueSlider from \"vue-slider-component\";\nimport {reactive} from \"vue\";\nimport {MODAL} from \"../../use/constants\";\n\nconst DUR_MAX = 480\n\nconst emit = defineEmits(['close-modal', 'tmpl-add-appts', 'tmpl-update-appt', 'show-info-modal'])\nconst props = defineProps({\n\tdata: {\n\t\ttype: Object,\n\t\trequired: true,\n\t\t// elm: HTMLDivElement | null,\n\t\t// cid: Number,\n\t\t// gridShift: Number,\n\t\t// k: Boolean,\n\t},\n})\n\nconst state = reactive({\n\tcount: 8,\n\tdurations: [30],\n\ttitle: '',\n})\n\nif (props.data.elm !== null) {\n\t// editing an appointment\n\tstate.durations = [...props.data.elm.dur]\n\tstate.title = props.data.elm.title\n}\n\n\nconst header = (() => {\n\tlet str = \"\"\n\tif (window.Intl && typeof window.Intl === \"object\") {\n\t\tconst lang = document.documentElement.hasAttribute('data-locale')\n\t\t\t\t? [document.documentElement.getAttribute('data-locale').replaceAll('_', '-'), document.documentElement.lang]\n\t\t\t\t: [document.documentElement.lang]\n\t\tif (props.data.elm === null) {\n\t\t\t// adding\n\t\t\tif (props.data.cid > -1) {\n\t\t\t\tconst d = new Date()\n\t\t\t\td.setDate(d.getDate() - d.getDay() + 1 - props.data.gridShift + props.data.cid)\n\t\t\t\tstr = new Intl.DateTimeFormat(lang,\n\t\t\t\t\t\t{weekday: \"long\"}).format(d)\n\t\t\t}\n\t\t} else if (props.data.elm.uTop !== undefined && props.data.elm.cID !== undefined) {\n\t\t\t// single appt. edit\n\n\t\t\tconst d = new Date()\n\t\t\td.setDate(d.getDate() - d.getDay() + 1 - props.data.gridShift + props.data.elm.cID)\n\n\t\t\tconst SH = 0 // same as grid.js\n\t\t\tconst minTotal = SH * 60 + props.data.elm.uTop * 5\n\t\t\tconst hours = (minTotal / 60) | 0\n\t\t\td.setHours(hours, minTotal - hours * 60)\n\t\t\tstr = new Intl.DateTimeFormat(lang,\n\t\t\t\t\t{weekday: \"long\", hour: \"2-digit\", minute: \"2-digit\"}).format(d)\n\t\t}\n\t}\n\treturn str\n})()\n\n\nconst handleUpdateShow = (evt) => {\n\tif (evt === false) {\n\t\temit('close-modal')\n\t}\n}\n\nconst addDurationHandler = () => {\n\n\tif (!Array.isArray(state.durations)) {\n\t\tstate.durations = [state.durations]\n\t}\n\n\tif (props.data.k === false && state.durations.length > 1) {\n\t\temit('show-info-modal', {\n\t\t\ttype: MODAL.CONTRIBUTION,\n\t\t\tmessage: t('appointments', 'More than two duration choices')\n\t\t})\n\t\treturn\n\t}\n\n\tif (state.durations.length > 7) return\n\n\tconst a = [...state.durations, DUR_MAX].sort((a, b) => a - b)\n\t// find the largest space\n\tlet s = 0\n\tfor (let sp = 0, spn = 0, i = 0, l = a.length - 1; i < l; i++) {\n\t\tspn = a[i + 1] - a[i]\n\t\tif (spn > sp) {\n\t\t\tsp = spn\n\t\t\ts = i\n\t\t}\n\t}\n\tstate.durations.push((((a[s] + ((a[s + 1] - a[s]) >> 1)) / 5) | 0) * 5)\n\n}\nconst removeDurationHandler = () => {\n\tif (Array.isArray(state.durations) && state.durations.length > 1) {\n\t\tstate.durations.splice(-1, 1)\n\t}\n}\n\nconst durationsTooltipFormatter = (v) => {\n\tconst h = (v / 60) | 0\n\tconst m = v - h * 60\n\treturn h + \":\" + (m > 9 ? \"\" + m : \"0\" + m)\n}\n\nconst handlePrimaryClick = () => {\n\tif (props.data.elm === null) {\n\t\temit('tmpl-add-appts', {\n\t\t\tcount: state.count,\n\t\t\tdur: Array.isArray(state.durations)\n\t\t\t\t\t? state.durations.sort((a, b) => a - b)\n\t\t\t\t\t: [state.durations],\n\t\t\ttitle: state.title,\n\t\t\tcid: props.data.cid\n\t\t})\n\t} else {\n\t\temit('tmpl-update-appt', {\n\t\t\tdur: Array.isArray(state.durations)\n\t\t\t\t\t? state.durations.sort((a, b) => a - b)\n\t\t\t\t\t: [state.durations],\n\t\t\ttitle: state.title,\n\t\t\tcIdx: props.data.elm.cIdx,\n\t\t\tcID: props.data.elm.cID\n\t\t})\n\t\temit('close-modal')\n\t}\n}\n\nconst handleDeleteClick = () => {\n\t// reuse event\n\temit('tmpl-update-appt', {\n\t\tcIdx: props.data.elm.cIdx,\n\t\tcID: props.data.elm.cID,\n\t\tdel: true\n\t})\n\temit('close-modal')\n}\n\nconst primaryButtonText = props.data.elm === null\n\t\t? t('appointments', 'Add')\n\t\t: t('appointments', 'OK')\n\n</script>\n\n<template>\n\t<NcModal\n\t\t\tstyle=\"z-index: 10002\"\n\t\t\tlabelId=\"srgdev_appts_editor_name\"\n\t\t\t:setReturnFocus=\"false\"\n\t\t\t:show=\"data !== null\"\n\t\t\t@update:show=\"handleUpdateShow\">\n\t\t<h4 id=\"srgdev_appts_editor_name\" v-if=\"header!==''\" class=\"tao-h4\">{{ header }}</h4>\n\t\t<div class=\"srgdev-appt-modal-wrap\" style=\"text-align: left\">\n\t\t\t<template v-if=\"props.data.elm===null\">\n\t\t\t\t<LabelAccordion\n\t\t\t\t\t\t:label=\"t('appointments', 'Number of Appointments')\"\n\t\t\t\t\t\tfor=\"atam-slider-count\"/>\n\t\t\t\t<vue-slider\n\t\t\t\t\t\t:min=\"1\"\n\t\t\t\t\t\t:max=\"32\"\n\t\t\t\t\t\ttooltip=\"always\"\n\t\t\t\t\t\ttooltipPlacement=\"bottom\"\n\t\t\t\t\t\tid=\"atam-slider-count\"\n\t\t\t\t\t\tclass=\"ps-slider appt-slider\"\n\t\t\t\t\t\tv-model=\"state.count\"/>\n\t\t\t</template>\n\t\t\t<div class=\"pml-cont\">\n\t\t\t\t<label class=\"slider-label\">{{ t('appointments', 'Duration (hours:minutes)') }}</label>\n\t\t\t\t<span class=\"pml-p\" @click=\"addDurationHandler\">+</span>\n\t\t\t\t<span class=\"pml-m\" @click=\"removeDurationHandler\">−</span>\n\t\t\t</div>\n\t\t\t<vue-slider\n\t\t\t\t\t:min=\"5\"\n\t\t\t\t\t:max=\"DUR_MAX\"\n\t\t\t\t\t:order=\"false\"\n\t\t\t\t\t:interval=\"5\"\n\t\t\t\t\t:process=\"false\"\n\t\t\t\t\ttooltip=\"always\"\n\t\t\t\t\ttooltipPlacement=\"bottom\"\n\t\t\t\t\t:tooltip-formatter=\"durationsTooltipFormatter\"\n\t\t\t\t\tclass=\"ps-slider appt-slider\"\n\t\t\t\t\tv-model=\"state.durations\"/>\n\t\t\t<LabelAccordion\n\t\t\t\t\t:label=\"t('appointments', 'Title')\"\n\t\t\t\t\tfor=\"atam-input-title\"/>\n\t\t\t<NcTextField\n\t\t\t\t\tid=\"atam-input-title\"\n\t\t\t\t\tplaceholder=\"Optional\"\n\t\t\t\t\t:value.sync=\"state.title\"\n\t\t\t\t\t:label-outside=\"true\"/>\n\t\t\t<div class=\"actions-cont\">\n\t\t\t\t<NcButton\n\t\t\t\t\t\tclass=\"actions-cont-button\"\n\t\t\t\t\t\t:aria-label=\"primaryButtonText\"\n\t\t\t\t\t\ttype=\"primary\"\n\t\t\t\t\t\t@click=\"handlePrimaryClick\">\n\t\t\t\t\t{{ primaryButtonText }}\n\t\t\t\t</NcButton>\n\t\t\t\t<NcButton\n\t\t\t\t\t\tv-if=\"props.data.elm!==null\"\n\t\t\t\t\t\tclass=\"actions-cont-button\"\n\t\t\t\t\t\t:aria-label=\"t('appointments', 'Delete')\"\n\t\t\t\t\t\t@click=\"handleDeleteClick\">\n\t\t\t\t\t{{ t('appointments', 'Delete') }}\n\t\t\t\t</NcButton>\n\t\t\t\t<NcButton\n\t\t\t\t\t\tclass=\"actions-cont-button\"\n\t\t\t\t\t\t:aria-label=\"t('appointments', 'Cancel')\"\n\t\t\t\t\t\t@click=\"handleUpdateShow(false)\">\n\t\t\t\t\t{{ t('appointments', 'Cancel') }}\n\t\t\t\t</NcButton>\n\t\t\t</div>\n\t\t</div>\n\t</NcModal>\n</template>\n\n<style scoped>\n\n.tao-h4 {\n\tfont-size: 100%;\n\tpadding: 0 0 1em 2em;\n\tborder-bottom: 1px solid var(--color-border);\n\tfont-weight: bold;\n\tmargin: 1em 0;\n}\n\n.appt-slider {\n\tmargin-bottom: 3em;\n\tmargin-right: 0;\n\tbox-sizing: content-box;\n}\n\n.pml-cont {\n\tposition: relative;\n}\n\n.pml-m,\n.pml-p {\n\tfont-size: 125%;\n\tposition: absolute;\n\theight: 1em;\n\twidth: 1em;\n\tline-height: 1em;\n\ttop: 50%;\n\tmargin-top: -.5em;\n\ttext-align: center;\n\tcursor: pointer;\n\tcolor: var(--color-text-light);\n}\n\n.pml-m:hover,\n.pml-p:hover {\n\tcolor: var(--color-main-text);\n\ttransform: scale(1.2);\n}\n\n.pml-p {\n\tright: 1.375em;\n}\n\n.pml-m {\n\tright: 0;\n}\n\n.actions-cont {\n\tdisplay: flex;\n\tflex-direction: row;\n\tmargin-top: 2em;\n}\n\n.actions-cont-button {\n\tmargin-right: 1em;\n\tmin-width: 5em;\n}\n</style>"],"sourceRoot":""}]);
